const sun         = new THREE.Vector3();
const skyUniforms = sky.material.uniforms;
const skyParams   = { turbidity:10, rayleigh:2, mieCoefficient:0.005, mieDirectionalG:0.8,
                      elevation:60, azimuth:180, exposure:1, atualizarCeu };
skyParams.turbidity = 0.00;
skyParams.rayleigh = 0.39;
skyParams.mieCoefficient = 0.100;
skyParams.mieDirectionalG = 1.00;
skyParams.elevation = 11.74;
skyParams.azimuth = -148.70;

function atualizarCeu (elev, azi){
  sun.setFromSphericalCoords(1, THREE.MathUtils.degToRad(90-elev), THREE.MathUtils.degToRad(azi));
  skyUniforms.sunPosition.value.copy(sun);
  renderer.toneMappingExposure = skyParams.exposure;
}

atualizarCeu(skyParams.elevation, skyParams.azimuth);

sky.material.userData = {
  skyUniforms,
  skyParams
};

