// ---------- VARIÁVEIS ----------
let currentAnimation=null, currentProgress=0;
const floatingCubes = [];
/* --- variáveis para as mensagens --- */
const palavras     = ['Eco', 'Ruptura', 'Silêncio'];  // texto que irá aparecer
let   indiceMsg    = 0;       // próxima palavra a mostrar
let   textoVisivel = false;   // se já cruzou os 50 %

const CUBES      = 10;      // nº de cubos
const SIZE       = 0.025;   // <<< cubos mais pequenos (antes era 0.05)
const WATER_Y    = 0.02;    // altura média
const MIN_DIST   = 0.2;     // mais perto
const MAX_DIST   = 0.8;     // menos dispersos

const forward = new THREE.Vector3();
const right   = new THREE.Vector3();
const up      = new THREE.Vector3(0,1,0);

for (let i = 0; i < CUBES; i++) {
  // direcção forward no plano XZ
  camera.getWorldDirection(forward).setY(0).normalize();
  // vector right perpendicular no plano
  right.crossVectors(up, forward).normalize();

  // distância aleatória + deslocamento lateral dentro do FOV horizontal
  const dist = THREE.MathUtils.randFloat(MIN_DIST, MAX_DIST);

  // metade do FOV horizontal em radianos
  const halfHFov = Math.atan(Math.tan(THREE.MathUtils.degToRad(camera.fov * 0.5)) * camera.aspect);
  const maxLat   = Math.tan(halfHFov) * dist;              // largura visível a essa distância
  const lateral  = THREE.MathUtils.randFloatSpread(maxLat * 2); // [-maxLat, +maxLat]

  // posição final
  const pos = camera.position.clone()
                .add(forward.clone().multiplyScalar(dist))
                .add(right.clone().multiplyScalar(lateral));
  pos.y = WATER_Y;

  // --- CUBO EXTERNO (TRANSPARENTE) ---
  const cubeMaterial = new THREE.MeshStandardMaterial({
    color: 0xff6600, // <<< cor de laranja
    transparent: true,
    opacity: 10,
    depthWrite: true
  });
  const cube = new THREE.Mesh(
    new THREE.BoxGeometry(SIZE, SIZE, SIZE),
    cubeMaterial
  );
  cube.position.copy(pos);
  cube.userData.fixedX = pos.x;
  cube.userData.fixedZ = pos.z;
  cube.visible = false;

  floatingCubes.push(cube);
  scene.add(cube);
}


