// ---------- ESTADO ----------
const oceanState={ mode:'Calmo' };
function applyOceanState(mode){

  if(currentAnimation) cancelAnimationFrame(currentAnimation);
  oceanState.mode = mode;

  // se vamos para Calmo parar vozes já
  

  const startProgress=currentProgress;
  const targetProgress= mode==='Tempestade'?100:0;
  const dur  = mode==='Tempestade'?30:10;
  const steps= 60*dur; let step=0;

  const ini={
    amp:water.material.uniforms.uWavesAmplitude.value,   freq:water.material.uniforms.uWavesFrequency.value,
    pers:water.material.uniforms.uWavesPersistence.value,lacu:water.material.uniforms.uWavesLacunarity.value,
    iter:water.material.uniforms.uWavesIterations.value, speed:water.material.uniforms.uWavesSpeed.value,
    trough:water.material.uniforms.uTroughColor.value.clone(),  surface:water.material.uniforms.uSurfaceColor.value.clone(),
    peak:water.material.uniforms.uPeakColor.value.clone(),      fresnelS:water.material.uniforms.uFresnelScale.value,
    fresnelP:water.material.uniforms.uFresnelPower.value,       turb:skyUniforms.turbidity.value,
    ray:skyUniforms.rayleigh.value, mie:skyUniforms.mieCoefficient.value,
    mieG:skyUniforms.mieDirectionalG.value, elev:skyParams.elevation, azi:skyParams.azimuth
  };
  const dst= mode==='Tempestade'?{
      amp:.037,freq:3.54,pers:.3,lacu:2.18,iter:3,speed:.2,
      trough:new THREE.Color(1,0,0),surface:new THREE.Color(1,0,0),peak:new THREE.Color(1,0,0),
      fresnelS:.39,fresnelP:3,turb:20,ray:.2,mie:.1,mieG:.99,elev:5,azi:90
    }:{
      amp:.004,freq:5,pers:.6,lacu:2.2,iter:4,speed:.2,
      trough:new THREE.Color(0,.66,1),surface:new THREE.Color(0,.19,.39),peak:new THREE.Color(0,0,0),
      fresnelS:.8,fresnelP:1.6,
      turb: 0.00,           // turbidez
      ray: 0.39,            // dispersão (Rayleigh)
      mie: 0.100,           // coef. mie
      mieG: 1.00,           // direcionalidade mie
      elev: 11.74,          // elevação solar
      azi: -148.70          // azimute solar
    };

  function anim(){
    if(oceanState.mode!==mode) return;
    const tRaw = ++step / steps;      // 0 → 1+  (linear)
    const t    = Math.min(tRaw, 1);   // nunca passa de 1

    // progresso
    currentProgress = THREE.MathUtils.lerp(startProgress, targetProgress, t);
    progressBar.style.width = `${currentProgress}%`;
    progressText.textContent = `Progresso: ${currentProgress.toFixed(0)}%`;

    // -------- mensagens e efeitos --------
    if(msgWin){
      // Piscar morse a vermelho dos 30% aos 50%
      if(currentProgress >= 30 && currentProgress < 50){
        msgTimers = {}; altStart = 0;

        // Morse para SOS: ... --- ...
        const morseSeq = [
          {symbol: '.', duration: 1},
          {symbol: ' ', duration: 1},
          {symbol: '.', duration: 1},
          {symbol: ' ', duration: 1},
          {symbol: '.', duration: 1},
          {symbol: ' ', duration: 3},
          {symbol: '-', duration: 3},
          {symbol: ' ', duration: 1},
          {symbol: '-', duration: 3},
          {symbol: ' ', duration: 1},
          {symbol: '-', duration: 3},
          {symbol: ' ', duration: 3},
          {symbol: '.', duration: 1},
          {symbol: ' ', duration: 1},
          {symbol: '.', duration: 1},
          {symbol: ' ', duration: 1},
          {symbol: '.', duration: 1},
          {symbol: ' ', duration: 7} // espaço entre palavras
        ];
        const unit = 80; // ms, ajusta para mais rápido/lento
        // Calcula o tempo total do ciclo
        const totalMorse = morseSeq.reduce((acc, cur) => acc + cur.duration, 0) * unit;
        let now = Date.now() % totalMorse;
        let acc = 0, idx = 0;
        for (; idx < morseSeq.length; idx++) {
          acc += morseSeq[idx].duration * unit;
          if (now < acc) break;
        }
        const symbol = morseSeq[idx]?.symbol || ' ';

        // Pisca o fundo: vermelho só durante ponto/traço, preto no resto
        msgWin.document.body.innerHTML = '';
        msgWin.document.body.style.background =
          (symbol === '-' || symbol === '.') ? '#f00' : '#000';

        // Remove linhas glitch durante morse
        const old = msgWin.document.body.querySelectorAll('.glitch-line');
        old.forEach(e => e.remove());
      }
      // A partir de 50%: glitch SEMPRE ativo
      else if(currentProgress >= 50 && currentProgress < 60){
        showGlitchEffect(msgWin, "36°34'N 21°8'E");
        msgWin.document.body.style.background = '#000';
        createGlitchLines(msgWin, 18);
      }
      else if(currentProgress >= 60 && currentProgress < 70){
        showGlitchEffect(msgWin, "35°30'N 12°36'E");
        msgWin.document.body.style.background = '#000';
        createGlitchLines(msgWin, 18);
      }
      else if(currentProgress >= 70){
        if (!msgLoopActive) {
          msgLoopActive = true;
          loopMensagens();
        }
      }
      // Quando chega a 100%, fixa o último texto

      // Fora destes intervalos, limpa
      else{
        msgWin.document.body.textContent = '';
        msgWin.document.body.style.background = '#000';
        msgTimers = {}; altStart = 0;
        msgLoopActive = false;
        // Remove linhas glitch fora dos intervalos
        const old = msgWin.document.body.querySelectorAll('.glitch-line');
        old.forEach(e => e.remove());
      }
    }

    // ---------- SOM VOZES ----------
    const fade = THREE.MathUtils.clamp((currentProgress - 35) / 65, 0, 1);
    // (abaixo de 35 % → 0; 35 %-100 % → 0→1)

    stormAudio.volume = fade;
    stormAudio.playbackRate = 1 + 0.15 * fade;

    if (fade > 0 && stormAudio.paused) {
      stormAudio.play();
    }
    if (fade === 0 && !stormAudio.paused) {
      stormAudio.pause();
      stormAudio.currentTime = 0;
    }


    // 3 — Actualiza os uniforms só até 100 %
    if (tRaw < 1) {
      water.material.uniforms.uWavesAmplitude.value = THREE.MathUtils.lerp(ini.amp, dst.amp, t);
      water.material.uniforms.uWavesFrequency.value = THREE.MathUtils.lerp(ini.freq, dst.freq, t);
      water.material.uniforms.uWavesPersistence.value = THREE.MathUtils.lerp(ini.pers, dst.pers, t);
      water.material.uniforms.uWavesLacunarity.value = THREE.MathUtils.lerp(ini.lacu, dst.lacu, t);
      water.material.uniforms.uWavesIterations.value = THREE.MathUtils.lerp(ini.iter, dst.iter, t);
      water.material.uniforms.uWavesSpeed.value = THREE.MathUtils.lerp(ini.speed, dst.speed, t);

      water.material.uniforms.uTroughColor.value.lerpColors(ini.trough, dst.trough, t);
      water.material.uniforms.uSurfaceColor.value.lerpColors(ini.surface, dst.surface, t);
      water.material.uniforms.uPeakColor.value.lerpColors(ini.peak, dst.peak, t);

      water.material.uniforms.uFresnelScale.value = THREE.MathUtils.lerp(ini.fresnelS, dst.fresnelS, t);
      water.material.uniforms.uFresnelPower.value = THREE.MathUtils.lerp(ini.fresnelP, dst.fresnelP, t);

      skyUniforms.turbidity.value = THREE.MathUtils.lerp(ini.turb, dst.turb, t);
      skyUniforms.rayleigh.value = THREE.MathUtils.lerp(ini.ray, dst.ray, t);
      skyUniforms.mieCoefficient.value = THREE.MathUtils.lerp(ini.mie, dst.mie, t);
      skyUniforms.mieDirectionalG.value = THREE.MathUtils.lerp(ini.mieG, dst.mieG, t);
      skyParams.elevation = THREE.MathUtils.lerp(ini.elev, dst.elev, t);
      skyParams.azimuth = THREE.MathUtils.lerp(ini.azi, dst.azi, t);
      atualizarCeu(skyParams.elevation, skyParams.azimuth);
    }

    // ---------- COR DAS CRISTAS ----------
    const lin = THREE.MathUtils.clamp((currentProgress - 75) / 25, 0, 1);
    const trans = Math.pow(lin, 0.15);
    water.material.uniforms.uOceanTransition.value = trans;
    console.log('progresso', currentProgress.toFixed(0), 'trans', trans.toFixed(2));


    // 4 — Agenda o próximo frame de forma segura
    if (tRaw < 1) currentAnimation = requestAnimationFrame(anim);
  }
  anim();
}
