// ---------- FUNÇÕES DE ONDA ----------
function alturaOnda(x, z, t) {
  const { uWavesAmplitude:  { value: A },
          uWavesFrequency:  { value: F },
          uWavesPersistence:{ value: P },
          uWavesLacunarity: { value: L },
          uWavesIterations: { value: N },
          uWavesSpeed:      { value: S } } = water.material.uniforms;

  let amp = A, freq = F, h = 0;
  for (let i = 0; i < N; i++) {
    h += Math.sin((x + z) * freq + t * S) * amp;
    amp *= P;
    freq *= L;
  }
  return h;
}
const tmpNormal = new THREE.Vector3();
function surfaceInfo(x, z, t) {
  // Amostra FBM igual ao shader
  const { uWavesAmplitude:{value:A}, uWavesFrequency:{value:F},
          uWavesPersistence:{value:P}, uWavesLacunarity:{value:L},
          uWavesIterations:{value:N}, uWavesSpeed:{value:S} } = water.material.uniforms;

  let amp=A, freq=F, h=0, dHdX=0, dHdZ=0;
  for(let i=0;i<N;i++){
    const w = (x+z)*freq + t*S;
    const s = Math.sin(w), c = Math.cos(w);
    h    += s * amp;
    dHdX += c * amp * freq;
    dHdZ += c * amp * freq;
    amp*=P; freq*=L;
  }
  tmpNormal.set(-dHdX,1,-dHdZ).normalize();
  return { h, normal: tmpNormal };
}
function actualizarCubos(t){
  const blinkSpeed = 5;
  const darkOrange  = new THREE.Color(0xff6600); // laranja escuro
  const brightOrange = new THREE.Color(0xffa500); // laranja vivo

  floatingCubes.forEach((c, index)=>{
    let targetOpacity = 0;
    if (oceanState.mode === 'Tempestade') {
      const delay = index * 1.5; // cada cubo espera mais 1.5% de progresso (ajusta ao teu gosto)
      const fadeStart = 40 + delay;
      const fadeEnd = fadeStart + 2; // fade-in rápido
      targetOpacity = THREE.MathUtils.smoothstep(currentProgress, fadeStart, fadeEnd);
    } else {
      targetOpacity = THREE.MathUtils.smoothstep(currentProgress, 30, 40);
    }
    c.material.opacity = targetOpacity;
    c.visible = targetOpacity > 0.01;

    if(!c.visible) return;

    // POSIÇÃO VERTICAL
    const y = alturaOnda(c.userData.fixedX, c.userData.fixedZ, t);
    c.position.y = y + 0.01;

    // --- NOVO BLOCO: calcular normal da onda ---
    const EPS = 0.02; // 6-7× maior
    const hL = alturaOnda(c.userData.fixedX - EPS, c.userData.fixedZ, t);
    const hR = alturaOnda(c.userData.fixedX + EPS, c.userData.fixedZ, t);
    const hD = alturaOnda(c.userData.fixedX, c.userData.fixedZ - EPS, t);
    const hU = alturaOnda(c.userData.fixedX, c.userData.fixedZ + EPS, t);

    const dHdX = (hR - hL) / (2 * EPS);
    const dHdZ = (hU - hD) / (2 * EPS);

    const normal = new THREE.Vector3(-dHdX, 1, -dHdZ).normalize();

    // --- Alinhar o cubo com a normal da onda ---
    const q = new THREE.Quaternion().setFromUnitVectors(
      new THREE.Vector3(0, 1, 0), normal
    );
    c.quaternion.copy(q); // acompanha instantaneamente

    // --- COR: mantém sempre laranja escuro ---
    c.material.color.copy(darkOrange);
  });
}

// ---------- LOOP PRINCIPAL ----------
function animate(){
  const t=clock.getElapsedTime();

  // Interpolação da posição da câmara conforme o progresso
  const lerpFactor = currentProgress / 100;
  camera.position.lerpVectors(cameraInitialPos, cameraFinalPos, lerpFactor);

  // ... resto do animate ...
  water.update(t); ground.update(t); controls.update();
  actualizarCubos(t);
  water.visible=false; cubeCam.position.copy(water.position); cubeCam.update(renderer,scene); water.visible=true;
  water.material.uniforms.uLightDirection.value.copy(sun).normalize();
  water.material.uniforms.uLightColor.value.copy(dirLight.color);
  const p = camera.position;
  coordsDiv.textContent = `Câmara: x=${p.x.toFixed(3)} y=${p.y.toFixed(3)} z=${p.z.toFixed(3)}`;

  // --- TESTE: FIXAR EXPOSIÇÃO ---
  renderer.toneMappingExposure = 1.0;
  // --- FIM TESTE ---

  renderer.render(scene,camera); requestAnimationFrame(animate);
}

applyOceanState('Calmo');
animate();

let webcamStream, video, model;
let lastDetected = false;

async function setupWebcamDetection() {
  video = document.createElement('video');
  video.style.display = 'none';
  document.body.appendChild(video);

  webcamStream = await navigator.mediaDevices.getUserMedia({ video: true });
  video.srcObject = webcamStream;
  await video.play();

  model = await cocoSsd.load();
  detectLoop();
}

async function detectLoop() {
  if (!model || !video) return;
  const predictions = await model.detect(video);
  const personDetected = predictions.some(p => p.class === 'person' && p.score > 0.5);

  if (personDetected && !lastDetected) {
    applyOceanState('Tempestade');
    lastDetected = true;
  } else if (!personDetected && lastDetected) {
    applyOceanState('Calmo');
    lastDetected = false;
  }
  setTimeout(detectLoop, 1000); // verifica a cada segundo
}

setupWebcamDetection();

function glitchText(text, intensity = 0.3) {
  const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789@#$%&?!';
  return text.split('').map(c => {
    if (c !== ' ' && Math.random() < intensity) {
      return chars[Math.floor(Math.random() * chars.length)];
    }
    return c;
  }).join('');
}

