let altIndex = 0;
let altStart = 0;

function showGlitchEffect(msgWin, text) {
  if (glitchAnimId) cancelAnimationFrame(glitchAnimId);
  msgWin.document.body.innerHTML = `
    <div style="
      width:100vw; height:100vh; display:flex; align-items:center; justify-content:center;
      background:#000; margin:0; padding:0; overflow:hidden;">
      <span class="glitch" style="
        position:relative;
        display:inline-block;
        color:#fff;
        font-size:8vw;
        font-family:Arial,Helvetica,sans-serif;
        font-weight:bold;
        letter-spacing:0.05em;
        text-align:center;
        width:100vw;
        ">
        <span class="glitch-main";">${text}</span>
        <span class="glitch-layer" style="position:absolute;left:0;top:0;width:100%;height:100%;color:#f0f;opacity:.5;pointer-events:none;"></span>
        <span class="glitch-layer" style="position:absolute;left:0;top:0;width:100%;height:100%;color:#0ff;opacity:.5;pointer-events:none;"></span>
      </span>
    </div>
  `;
  createGlitchLines(msgWin, 18);
  const glitchText = msgWin.document.body.querySelector('.glitch');
  const main = glitchText.querySelector('.glitch-main');
  const layers = Array.from(glitchText.querySelectorAll('.glitch-layer'));
  const baseHTML = main.innerHTML;
  const altChars = 'AÁÀÃÂBCÇDEÉÊFGHIÍJKLMNOÓÕÔPQRSTUÚVWXYZ0123456789!@#$%&∑∂∆¥µ¶∞§';

  function randomChar(c) {
    if (c === ' ' || c === '\n') return c;
    return altChars[Math.floor(Math.random() * altChars.length)];
  }

  // Faz glitch só ao texto, não às tags HTML (inclui <br>)
  function glitchifyHTML(html, prob = 0.15) {
    let out = '';
    let inTag = false;
    for (let c of html) {
      if (c === '<') inTag = true;
      if (!inTag && c !== ' ' && Math.random() < prob) out += randomChar(c);
      else out += c;
      if (c === '>') inTag = false;
    }
    return out;
  }

  function animateGlitch() {
    layers.forEach((layer, i) => {
      layer.innerHTML = glitchifyHTML(baseHTML, 0.18 + i*0.03);
      layer.style.transform =
        `translate(${(Math.random()*6-3)}px, ${(Math.random()*4-2)}px) skew(${Math.random()*10-5}deg)`;
    });
    glitchAnimId = requestAnimationFrame(animateGlitch);
  }
  layers.forEach(layer => layer.innerHTML = baseHTML);
  animateGlitch();
}

function createGlitchLines(msgWin, numLines = 18) {
  if (linesAnimId) cancelAnimationFrame(linesAnimId);
  // Remove linhas antigas
  const old = msgWin.document.body.querySelectorAll('.glitch-line');
  old.forEach(e => e.remove());

  const w = msgWin.innerWidth || 400;
  const h = msgWin.innerHeight || 260;

  for(let i=0; i<numLines; i++) {
    const line = msgWin.document.createElement('div');
    line.className = 'glitch-line';
    const top = Math.random() * 100;
    const width = 60 + Math.random() * 40; // 60% a 100% largura
    const left = Math.random() * (100 - width);
    line.style.cssText = `
      position:absolute;
      left:${left}vw;
      top:${top}vh;
      width:${width}vw;
      height:2px;
      background:linear-gradient(90deg,#0ff,#f0f,#0ff);
      opacity:${0.15 + Math.random()*0.25};
      filter:blur(${Math.random()*1.5}px);
      pointer-events:none;
      z-index:1;
      transition:none;
    `;
    msgWin.document.body.appendChild(line);
  }

  // Anima as linhas
  function animateLines() {
    msgWin.document.querySelectorAll('.glitch-line').forEach(line => {
      const dx = (Math.random()-0.5)*2;
      const dy = (Math.random()-0.5)*1;
      const blur = Math.random()*2;
      line.style.transform = `translate(${dx}px,${dy}px) skewX(${(Math.random()-0.5)*8}deg)`;
      line.style.filter = `blur(${blur}px)`;
      line.style.opacity = 0.15 + Math.random()*0.25;
    });
    linesAnimId = requestAnimationFrame(animateLines);
  }
  animateLines();
}

let glitchAnimId = null;
let linesAnimId = null;

let msgLoopActive = false;
function loopMensagens() {
  if (!msgWin) return;
  // Só ativa se currentProgress >= 70
  if (currentProgress >= 70) {
    if (!msgTimers.lastPick || Date.now() - msgTimers.lastPick > 3000) {
      altIndex = Math.floor(Math.random() * altTexts.length);
      msgTimers.lastPick = Date.now();
    }
    showGlitchEffect(msgWin, altTexts[altIndex].text);
    msgWin.document.body.style.background = '#000';
    createGlitchLines(msgWin, 18);
  }
  if (msgLoopActive) setTimeout(loopMensagens, 100); // ajusta o intervalo se quiseres
}